aside {
  box-sizing: border-box;
  background-color: #252526;
}
aside h1 {
  display: block;
  height: 36px;
  line-height: 36px;
  margin: 0 8px;
  padding-left: 12px;
  font-family: Segoe UI;
  font-size: 11px;
  font-weight: normal;
  color: #bbbbbb;
  text-transform: uppercase;
  user-select: none;
  cursor: default;
}



